<template>
  <div class="enterprise-info-container">
    <el-dialog title :visible.sync="centerDialogVisible" :show-close="false" width="23%" center>
      <div style="text-align:center">
        <div class="loading">
          <i style="font-size:60px;color:#faad14;" class="el-icon-warning"></i>
        </div>
        <div class="loading-text"></div>
        <div style="text-align:center">
          <el-button
            style="height:40px;line-height:0px;margin:35px 0 0 0"
            plain
            @click="centerDialogVisible = false"
          ></el-button>
          <el-button
            style="height:40px;line-height:0px;margin:35px 0 0 30px"
            type="primary"
            @click="centerDialogVisible = false"
          ></el-button>
        </div>
      </div>
    </el-dialog>
    <div class="body">
      <div class="head">
        <p></p>
        <el-progress
          :text-inside="true"
          :stroke-width="15"
          percentage="0"
        ></el-progress>
      </div>
      <div class="form">
        <p class="header">
          <span class="title"></span>
          <el-button
            class="edit-btn main"
            @click="editEnterpriseRegisterInfo"
            v-if="enterpriseInfoEditMode"
          ></el-button>
        </p>
        <div class="display" v-if="enterpriseInfoEditMode">
          <ul>
            <li>
              <span></span>
              <span class="spanSecond">{{companyInfo.fullName}}</span>
            </li>
            <li>
              <span></span>
              <span class="spanSecond">{{companyInfo.shortName}}</span>
            </li>
            <li>
              <span></span>
              <span class="spanSecond">{{companyInfo.size}}</span>
            </li>
            <li>
              <span></span>
              <span class="spanSecond">{{companyInfo.nature}}</span>
            </li>
            <li>
              <span></span>
              <span class="spanSecond">{{companyInfo.industrySecondary}}</span>
            </li>
            <li>
              <span></span>
              <span class="spanSecond">{{companyInfo.address.district}}</span>
            </li>
            <li>
              <span></span>
              <span class="spanSecond">{{companyInfo.description}}</span>
            </li>
            <li>
              <span> LOGO</span>
              <span>
                <img class="logo" v-if="companyInfo.logoUrl" />
              </span>
            </li>
          </ul>
        </div>
        <el-form
          class="form-container"
          ref="companyInfo"
          :rules="companyInfoFormRules"
          :model="companyInfo"
          label-width="140px"
          v-else
        >
          <el-form-item label="">
            <span>{{ companyInfo.fullName }}</span>
          </el-form-item>
          <el-form-item label="" prop="shortName">
            <el-input style="width:240px" v-model="companyInfo.shortName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="size">
            <el-select style="width:240px" v-model="companyInfo.size" placeholder="">
              <el-option
                v-for="item in this.optionList.companySize"
                :key="item.code"
                :label="item.tag"
                :value="item.tag"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="nature">
            <el-select style="width:240px" v-model="companyInfo.nature" placeholder="">
              <el-option
                v-for="item in optionsNature"
                :key="item.code"
                :label="item.tag"
                :value="item.tag"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="industry">
            <el-cascader
              style="width:240px"
              :props="props"
              :options="industryList"
              v-model="companyInfo.industry"
              placeholder=""
            ></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="city">
            <el-cascader
              v-model="companyInfo.city"
              style="width:240px"
              :props="props"
              :options="cityList"
            ></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="detail">
            <el-input
              type="textarea"
              style="width:500px;margin:20px 0 0 0"
              :rows="4"
              v-model="companyInfo.detail"
              placeholder=""
            ></el-input>
          </el-form-item>
          <!-- <el-form-item label="">
            <el-input
              style="margin-right: 10px;width:240px"
              placeholder=""
              v-model="companyInfo.address.latitude"
            ></el-input>
            <el-input
              placeholder=""
              style="width:240px"
              v-model="companyInfo.address.longitude"
            ></el-input>
          </el-form-item>-->
          <el-form-item label="" prop="description">
            <el-input
              style="width:500px"
              type="textarea"
              :rows="4"
              placeholder=""
              v-model="companyInfo.description"
            ></el-input>
          </el-form-item>
          <el-form-item label=" LOGO" prop="file">
            <el-upload
              class="avatar-upload"
              :action="uploadUrl"
              :data="uploadData"
              :headers="myHeaders"
              style="margin-left:0px"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
            >
              <img v-if="imageUrl" :src="imageUrl" class="avatar" />
              <i v-else style="border:1px solid #dcdfe6;margin:0 200px 0 0" class="el-icon-plus avatar-uploader-icon"></i>
              <div class="el-upload__tip">pngjpgjpeg 3M</div>
              <div class="el-upload__tip"></div>
            </el-upload>
            <!-- <img
              ref="preview-logo"
              class="logo"
              :src="companyInfo.logoUrl"
              v-if="companyInfo.logoUrl"
            />
            <el-button size="small" type="primary"></el-button>
            <div class="el-upload__tip">pngjpgjpeg 3M</div>
            <div class="el-upload__tip"></div>
            <input class="image-input" ref="input" type="file" accept="image/*" />
            <el-dialog
              title=""
              :visible.sync="lOGOUploadModalVisible"
              :close-on-click-modal="false"
              :close-on-press-escape="false"
              width="30%"
            >
              <vue-cropper ref="cropper" :src="imgSrc" :aspectRatio="1"></vue-cropper>
              <span slot="footer" class="dialog-footer">
                <el-button></el-button>
                <el-button type="primary" @click="cropImage"></el-button>
              </span>
            </el-dialog>-->
          </el-form-item>
          <div class="operations">
            <el-button type="primary" class="main" @click="updateCompany"></el-button>
            <el-button @click="clearAndReload"></el-button>
          </div>
        </el-form>
        <span class="line"></span>
        <p class="header">
          <span class="title"></span>
          <el-button
            class="edit-btn main"
            @click="editEnterpriseInfo"
            v-if="enterpriseRegisterInfoEditMode"
          ></el-button>
        </p>
        <div class="display" v-if="enterpriseRegisterInfoEditMode">
          <ul>
            <li>
              <span></span>
              <span class="spanSecond">{{ 1 }}</span>
            </li>
            <li>
              <span></span>
              <span class="spanSecond">{{ 2 }}</span>
            </li>
            <li>
              <span></span>
              <span class="spanSecond">{{ 3 }}</span>
            </li>
            <li>
              <span></span>
              <span class="spanSecond">13</span>
            </li>
            <li>
              <span></span>
              <!-- <span><img :src="companyInfo.audit.licenseUrl.accessUrl" /></span> -->
            </li>
          </ul>
        </div>
        <el-form
          class="form-container"
          ref="form"
          :rules="auditInfoFormRules"
          :model="companyInfo.audit"
          label-width="140px"
          v-else
        >
          <el-form-item label="" prop="companyName">
            <el-input
              style="width:240px"
              v-model="companyInfo.audit.companyName"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="registeredAddress">
            <el-input
              style="width:240px"
              v-model="companyInfo.audit.registeredAddress"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="uniformSocialCreditCode">
            <el-input
              style="width:240px"
              maxlength="18"
              v-model="companyInfo.audit.uniformSocialCreditCode"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="enterpriseForm">
            <el-cascader
              style="width:240px"
              :options="enterpriseTypes"
              v-model="companyInfo.audit.enterpriseForm"
              placeholder=""
            ></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="file">
            <el-upload
              class="upload"
              :action="uploadCompanyFile"
              :file-list="auditTempFile"
              :on-success="dealWithUploadLicense"
              :with-credentials="true"
              :limit="1"
              list-type="picture"
            >
              <el-button size="small" type="primary"></el-button>
              <div class="el-upload__tip" slot="tip">pngjpgjpeggif 3M</div>
              <div
                class="el-upload__tip"
                slot="tip"
              ></div>
              <div class="el-upload__tip warning" slot="tip"></div>
            </el-upload>
          </el-form-item>
          <div class="operations">
            <el-button type="primary" class="main" @click="onSubmitAuditInfo"></el-button>
            <el-button @click="clearAndReload"></el-button>
          </div>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
let token = Cookies.get("token");
import Cookies from "js-cookie";
import industry from "../../assets/industry.json";
import city from "../../assets/city.json";
import option from "../../assets/option.json";
export default {
  name: "home",
  data() {
    return {
      myHeaders: { "Auth-Token": token },
      uploadData: {
        label: "company-logo"
      },
      lOGOUploadModalVisible: false,
      centerDialogVisible: false,
      enterpriseInfoEditMode: true,
      enterpriseRegisterInfoEditMode: true,
      props: {
        value: "tag",
        label: "tag",
        children: "children"
      },
      imgSrc: "",
      cityList: [],
      industryList: [],
      optionList: [],
      companyInfo: {
        completedPercent: 0,
        detail: "",
        fullName: "",
        industry: [],
        nature: "",
        shortName: "",
        size: "",
        addressId: "",
        logoUrl: "",
        city: [],
        description: "",
        audit: {
          companyName: "",
          enterpriseForm: "",
          licenseUrl: {
            accessUrl: ""
          },
          registeredAddress: "",
          uniformSocialCreditCode: ""
        }
      },
      optionsNature: [
        {
          code: 0,
          tag: ""
        },
        {
          code: 1,
          tag: ""
        },
        {
          code: 2,
          tag: ""
        },
        {
          code: 3,
          tag: ""
        },
        {
          code: 4,
          tag: ""
        }
      ],
      companyInfoFormRules: {
        size: [{ required: true, message: "", trigger: "blur" }],
        nature: [
          { required: true, message: "", trigger: "blur" }
        ],
        industry: [
          { required: true, message: "", trigger: "blur" }
        ],
        "address.province": [
          { required: true, message: "", trigger: "blur" }
        ],
        description: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    //
    brief() {
      this.$http
        .get("/business-core/companyes/brief")
        .then(res => {
          if (res.data.code == "200") {
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //
    companyDetail() {
      this.$http
        .get("/business-core/companyes")
        .then(res => {
          let response = res.data.data;
          if (res.data.code == "200") {
            this.companyInfo = response;
            this.companyInfo.industry = [
              response.industryFirst,
              response.industrySecondary
            ];
            this.companyInfo.city = [
              response.address.province,
              response.address.city,
              response.address.district
            ];
            this.companyInfo.detail = response.address.detail;
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //
    updateCompany() {
      let params = {
        addressId: 130206,
        cert: null,
        companyAddressBody: {
          city: this.companyInfo.city[1],
          detail: this.companyInfo.detail,
          district: this.companyInfo.city[2],
          province: this.companyInfo.city[0]
        },
        companyId: 155,
        description: this.companyInfo.detail,
        fullName: this.companyInfo.fullName,
        industryCode: null,
        industryFirst: this.companyInfo.industry[0],
        industrySecondary: this.companyInfo.industry[1],
        logo: null,
        nature: this.companyInfo.nature,
        natureCode: null,
        shortName: this.companyInfo.shortName,
        size: this.companyInfo.size,
        sizeCode: null
      };
      this.$http
        .put("/business-core/companyes", params)
        .then(res => {
          let response = res.data.data;
          if (res.data.code == "200") {
            this.enterpriseInfoEditMode = true;
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    editEnterpriseRegisterInfo() {
      console.log("1313");
      this.enterpriseInfoEditMode = false;
      // if (this.enterpriseInfoEditMode) {
      //   this.$notify({
      //     title: "",
      //     message: "",
      //     type: "warning"
      //   });
      // } else {
      //   this.enterpriseInfoEditMode = false;
      // }
    },
    editEnterpriseInfo() {
      this.enterpriseRegisterInfoEditMode = false;
      // if (this.enterpriseRegisterInfoEditMode) {
      //   this.$notify({
      //     title: "",
      //     message: "",
      //     type: "warning"
      //   });
      // } else {
      //   this.enterpriseRegisterInfoEditMode = false;
      // }
    }
  },
  computed: {
    uploadUrl() {
      return '/file-service/files/upload';
    }
  },
  created() {
    this.cityList = city.data;
    this.industryList = industry.data;
    this.optionList = option.data;
    this.brief();
    this.companyDetail();
  }
};
</script>
<style lang="stylus" scoped>
.loading-text {
  font-family: PingFangSC-Medium;
  color: #111111;
  font-size: 20px;
  margin: 20px 0 0 0;
}

.enterprise-info-container {
  display: flex;
  flex-direction: column;
  margin-top: 0px;

  .nav {
    position: relative;
    flex: 0 0 70px;
  }

  .body {
    display: flex;
    width: 100%;
    justify-content: center;
    flex-direction: column;
    flex: 1;
    position: relative;
    background-color: white;
    margin: auto;

    .inline-top-item {
      margin-bottom: 10px;
    }

    .head {
      width: 240px;
      text-align: left;
      font-size: 14px;
      margin: 20px 0 20px 15px;
    }

    .form {
      padding: 10px;

      .form-container {
        .operations {
          text-align: right;
        }

        .image-input {
          display: none;
        }
      }

      .display {
        ul {
          list-style: none;
          text-align: left;
          font-size: 16px;
          padding: 0;

          li {
            margin: 40px 0;
            display: flex;

            span:first-child {
              flex: 0 0 150px;
              text-align: right;
            }

            .spanSecond {
              font-size: 14px;
              margin: 3px 0 0 0;
            }
          }
        }
      }

      img {
        max-height: 100px;
        margin-right: 20px;
        vertical-align: bottom;
      }

      .line {
        width: 100%;
        height: 1px;
        display: inline-block;
        background: lightgrey;
        margin: 20px 0;
      }

      button.full {
        width: 100%;
      }

      p {
        display: flex;
        flex-direction: row;
        margin-bottom: 20px;

        &.header {
          justify-content: space-between;
          margin: 10px 0 30px 5px;
          font-weight: bold;

          .title {
            display: inline-block;
            vertical-align: middle;
            margin-right: 3px;
            line-height: 38px;
          }
        }

        span {
          font-size: 22px;
        }
      }
    }
  }
}
</style>